.block {
  text-align: center;
}

.block__button {
  text-align: center;
  font-size: 25px;
  font-weight: 500;
  margin: 0px 30px;
  box-shadow: 0px 2px 0px 0px rgb(150, 150, 150);
}

.block__button_left:hover,
.block__button_right:hover {
  color: rgb(133, 72, 30);
  box-shadow: 0px 2px 0px 0px rgb(133, 72, 30);
}

.block__title{
  display: grid;
  text-align: center;

  padding: 18px;
  font-size: 25px;
  font-weight: 700;

  /* font-family: "Trebuchet MS", sans-serif; */
}

.block__title_font{
  font-family: "CMU SERIF", sans-serif;
}

.block__title_border{
  border-top: 1px solid rgb(150, 150, 150,.2);
  border-bottom: 1px solid rgb(150, 150, 150,.2);
}

.block__title_big{
  font-size: 100px;
}

.block__items {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 800px) 1fr;

  background-color: white;
  color: black;

  margin: 20px 10px;
  position: relative;
}

.block__items img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  object-fit: cover;
  opacity: 0.3;
}

.block__item {
  padding: 15px 20px;
  text-align: center;
  grid-column: 2/3;
  z-index: 2;
}

.block__subtitle {
  font-size: 24px;
  font-weight: 500;
}

.block__description {
  font-size: 22px;
  padding: 10px;

  font-family: "Arial", sans-serif;
}

a .block__button {
  text-decoration: none;
  color: black;
}

@media(max-width:768px){
  .block {
    text-align: center;
  }
  
  .block__button {
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    margin: 0px 30px;
    box-shadow: 0px 2px 0px 0px rgb(150, 150, 150);
  }
  
  .block__button_left:hover,
  .block__button_right:hover {
    color: rgb(133, 72, 30);
    box-shadow: 0px 2px 0px 0px rgb(133, 72, 30);
  }
  
  .block__title{
    display: grid;
    text-align: center;
  
    padding: 18px;
    font-size: 12px;
    font-weight: 700;
  
    /* font-family: "Trebuchet MS", sans-serif; */
  }
  
  .block__title_font{
    font-family: "CMU SERIF", sans-serif;
  }
  
  .block__title_border{
    border-top: 1px solid rgb(150, 150, 150,.2);
    border-bottom: 1px solid rgb(150, 150, 150,.2);
  }
  
  .block__title_big{
    font-size:20px;
  }
  
  .block__items {
    display: grid;
    grid-template: 1fr / 1fr minmax(auto, 600px) 1fr;
  
    background-color: white;
    color: black;
  
    margin: 20px 10px;
    position: relative;
  }
  
  .block__items img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  
    object-fit: cover;
    opacity: 0.3;
  }
  
  .block__item {
    padding: 15px 20px;
    text-align: center;
    grid-column: 2/3;
    z-index: 2;
  }
  
  .block__subtitle {
    font-size: 12px;
    font-weight: 500;
  }
  
  .block__description {
    font-size: 12px;
    padding: 10px;
  
    font-family: "Arial", sans-serif;
  }
  
  a .block__button {
    text-decoration: none;
    color: black;
  }
}